import { MDXRemote } from "next-mdx-remote";
import { getPost, getAllPosts } from "../../lib/mdx";
import Layout from "../../components/Layout";

export default function Post({ frontMatter, mdxSource }) {
  return (
    <Layout>
      <article className="prose lg:prose-lg max-w-none">
        <h1>{frontMatter.title}</h1>
        <p className="text-gray-500 text-sm">{frontMatter.date}</p>
        <MDXRemote {...mdxSource} />
      </article>
    </Layout>
  );
}

export async function getStaticPaths() {
  const sections = ["recipes", "experiences", "learnings"];
  const paths = sections.flatMap((section) =>
    getAllPosts(section).map((p) => ({
      params: { section, slug: p.slug },
    }))
  );
  return { paths, fallback: false };
}

export async function getStaticProps({ params }) {
  const { frontMatter, mdxSource } = await getPost(params.section, params.slug);
  return { props: { frontMatter, mdxSource } };
}
